import React from 'react'

function CartModal({ typeModal, handlerNot, handlerYes }) {

    const modalText = ["   ?", "   ?"]

    return (
        <div className="modal__cart">
            <div className="modal__text">
                {modalText[typeModal]}
            </div>
            <div className="modal__buttons">
                <button onClick={handlerNot} className="modal__button btn shd eff"></button>
                <button onClick={handlerYes} className="modal__button btn shd eff"></button>
            </div>
        </div>
    )
}

export default CartModal
